@import "../mixins";
.office__btn{
  margin-right: 20px;
}

.office__btn:last-child{
  margin: 0;
}

.offices-map{
  margin-top: 56px;
  &__inner{
    position: relative;
  }
}

.map{
  border-radius: 16px;
  height: 680px;
  overflow: hidden;
}

.map-modal{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 24px;
  left: 24px;
  width: 380px;
  background-color: #fff;
  border: 1px solid #D2D7DF;
  padding: 32px;
  border-radius: 12px;
  &._open{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    top: 24px;
    right: 24px;
    width: 24px;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__btn{
    margin-top: 10px;
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}


@media (max-width: 1000px) and (min-width: 751px){
  .contacts-filter .md\:col-span-6 {
    -ms-grid-column-span: 6;
    grid-column: span 6 / span 6;
  }
  .contacts-filter .md\:col-span-12 {
    -ms-grid-column-span: 12;
    grid-column: span 12 / span 12;
  }
}

.department-card{
  &__bottom{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__btn{
    margin-top: 40px;
  }
}

