.personal-card{
  padding: 24px;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #F3F5F7;
  border-radius: 12px;
  &--small{
    min-height: 280px;
    .personal-card__prof{
      margin-bottom: .5em;
    }
  }
  &--outline{
    border: 1px solid #D2D7DF;
    background: #FFF;
    .personal-card__links{
      background-color: #fff;
    }
  }
  &--red{
    background-color: #E81D25;
    color: #fff;
    .personal-card__links{
      background-color: #E81D25;
      color: #fff;
      svg{
        fill: #fff;
      }
    }
  }
  &--btn-bottom{
    .personal-card__nav{
      margin-top: auto;
    }
    .personal-card__links{
      top: unset;
      bottom: 0;
    }
  }
  &:hover{
    .personal-card__links{
      opacity: 1;
    }
  }
  &__nav{
    padding-top: 36px;
    display: flex;
    position: relative;
    width: 100%;
  }
  &__links{
    color: #90959E;
    position: absolute;
    top: 36px;
    left: 0;
    width: 100%;
    background-color: #F3F5F7;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    transition: all .3s;
    opacity: 0;
  }
  &__desc{
    &~.personal-card__nav{
      padding-top: 16px;
      .personal-card__links{
        top: 16px;
      }
    }
  }
  &__socials{
    padding-top: 16px;
    margin-top: auto;
    display: flex;
    .button__minor:before{
      display: none;
    }
  }
}


