@media screen and (max-width: 750px){
  .map{
    height: 480px;
  }

  .map-modal{
    bottom: 0;
    top: unset;
    left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    padding: 20px;
    border-radius:0;
    box-shadow: 0px -125px 35px 0px rgba(0, 0, 0, 0.00), 0px -80px 32px 0px rgba(0, 0, 0, 0.01), 0px -45px 27px 0px rgba(0, 0, 0, 0.03), 0px -20px 20px 0px rgba(0, 0, 0, 0.04), 0px -5px 11px 0px rgba(0, 0, 0, 0.05);
    &__close{
      top: 20px;
      right: 20px;
      width: 20px;
    }
  }

  .new-department{
    min-height: 400px;
    background-size: 212px auto;
  }

  .warehouses{
    min-height: 500px;
    background-size: 351px auto;
    background-position: right 0 bottom 22px;
    &__bottom{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
    }
    &__arrow,
    &__btn{
      margin: 0;
    }
  }

  .personal-card{
    padding: 16px;
    min-height: 360px;
  }
}


